.wrap_header {
  display: block;
}
.wrap_header .line {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0 15px;
}
.wrap_header .line .bugs_banner,
.wrap_header .line .no_formed_journals_banner {
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  line-height: normal;
  font-size: 12px;
  text-align: center;
  padding: 8px 16px;
  margin: 0 7.5px;
  white-space: nowrap;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.wrap_header .line .bugs_banner .hidden_part,
.wrap_header .line .no_formed_journals_banner .hidden_part {
  margin-left: 30px;
  transition-delay: 1s;
}
.wrap_header .line .bugs_banner .inc_word,
.wrap_header .line .no_formed_journals_banner .inc_word {
  margin: 0 3px;
}
.wrap_header .line .bugs_banner:before,
.wrap_header .line .no_formed_journals_banner:before {
  content: "";
  flex-grow: 1;
  flex-shrink: 0;
  margin-right: 7px;
}
.wrap_header .line .no_formed_journals_banner {
  background: #ef5350;
  color: #ffffff;
  margin-right: 36px;
  width: 188px;
  transition: all 1s;
  transition-delay: 0.3s;
}
.wrap_header .line .no_formed_journals_banner:before {
  background: url("../assets/images/fire.svg") no-repeat;
  width: 12px;
  height: 12px;
}
.wrap_header .line .no_formed_journals_banner:hover {
  transition: all 1s;
  overflow: hidden;
  width: 447px;
}
.wrap_header .line .no_formed_journals_banner:hover .hidden_part {
  transition-delay: 0.1s;
  margin-left: 0;
}
.wrap_header .line .bugs_banner {
  background: #ffc700;
  color: #232323;
  width: 188px;
  transition: all 1s;
  transition-delay: 0.3s;
}
.wrap_header .line .bugs_banner:before {
  background: url("../assets/images/mark.svg") no-repeat;
  width: 12px;
  height: 12px;
}
.wrap_header .line .bugs_banner:hover {
  transition: all 1s;
  width: 447px;
}
.wrap_header .line .bugs_banner:hover .hidden_part {
  transition-delay: 0.1s;
  margin-left: 0;
}
.wrap_header .line .days {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 14px;
  color: #3a3a3a;
  margin-left: 15px;
  white-space: nowrap;
}
.wrap_header .line .days svg {
  margin-right: 5px;
}
.wrap_header .line .days .time {
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: normal;
  font-size: 10px;
  color: #626262;
}
.wrap_header .line .days .time .count_day,
.wrap_header .line .days .time .count_hours {
  margin-left: 7px;
  width: 25px;
  height: 25px;
  border: 1px solid #ffb72a;
  box-sizing: border-box;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  flex-shrink: 0;
  font-size: 13px;
  color: #3a3a3a;
}
.wrap_header .line .not_ready {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 14px;
  color: #3a3a3a;
}
.wrap_header .line .not_ready svg {
  margin-right: 7px;
}
.wrap_header .line .warn {
  background: #fcd202;
  padding: 10px 73px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  font-weight: bold;
  line-height: normal;
  font-size: 14px;
  color: #4d4d4d;
}
.wrap_header .line .right_content {
  width: 950px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 15px;
  position: relative;
}
.wrap_header .line .right_content .plain {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 11px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  font-weight: bold;
  color: #676767;
  white-space: nowrap;
  flex-shrink: 0;
}
.wrap_header .line .right_content .plain .text_plain {
  margin-left: 10px;
}
.wrap_header .line .right_content .plain {
  cursor: pointer;
}
.wrap_header .line .right_content .dots {
  flex-shrink: 0;
  cursor: pointer;
  padding: 10px 15px;
}
.wrap_header .line .right_content .dots svg {
  z-index: -1;
}
.wrap_header .line .menu {
  position: absolute;
  top: 47px;
  right: -4px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 172px;
  background: #ffffff;
  border: 1px solid #dadada;
  box-sizing: border-box;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.12);
  border-radius: 5px;
  z-index: 3;
}
.wrap_header .line .menu .menu_element {
  display: flex;
  align-items: center;
  padding: 10px 16px;
  white-space: nowrap;
  cursor: pointer;
  color: #838383;
  text-decoration: none;
}
.wrap_header .line .menu .menu_element .btn_text {
  margin-left: 12px;
  line-height: 21px;
  font-size: 14px;
  letter-spacing: 0.02em;
}
.wrap_header .line .menu .menu_element:hover {
  background: #f7f7f7;
}
.wrap_header .line .menu .menu_element:first-child {
  border-radius: 5px 5px 0 0;
}
.wrap_header .line .menu .menu_element:last-child {
  border-radius: 0 0 5px 5px;
}
.wrap_header .line .menu .menu_element .icon.btn_refresh {
  height: 13px;
  width: 13px;
}
