.preloader {
  position: fixed;
  top: 65px;
  left: 22px;
  right: 22px;
  bottom: 27px;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.preloader .images {
  position: relative;
  width: 500px;
  height: 500px;
}
.preloader .images .wrap_img1 {
  position: absolute;
  top: 102px;
  left: 270px;
  z-index: 3;
  animation: move1 3s infinite;
}
.preloader .images .wrap_img2 {
  position: absolute;
  top: 125px;
  left: 208px;
  z-index: 2;
  animation: move2 3s infinite;
  animation: move1 3s infinite;
}
.preloader .images .wrap_img3 {
  position: absolute;
  top: 150px;
  left: 150px;
  z-index: 1;
  animation: move3 3s infinite;
}
.preloader .images .signature {
  position: absolute;
  bottom: 0;
  line-height: 34px;
  font-size: 26px;
  text-align: center;
  color: #3a3a3a;
}
.preloader .images .will_be_formed {
  width: 924px;
  position: absolute;
  bottom: 0;
  left: -212px;
}
.preloader .images .will_be_formed .head {
  font-weight: bold;
  line-height: 35px;
  font-size: 24px;
  text-align: center;
  color: #3a3a3a;
}
.preloader .images .will_be_formed .change_date {
  font-weight: normal;
  line-height: 22px;
  font-size: 18px;
  text-align: center;
  color: #3a3a3a;
}
@keyframes move2 {
  100% {
    top: -149px;
    left: 226px;
  }
}
@keyframes move1 {
  10% {
    top: -149px;
    left: 226px;
  }
  20% {
    top: -135px;
    left: 148px;
  }
  30% {
    top: 150px;
    left: 150px;
  }
}
@keyframes move3 {
  10% {
    z-index: 4;
  }
  75% {
    z-index: 1;
  }
}
