.wrap_main_content_dynamics {
  position: relative;
}
.wrap_main_content_dynamics .main_content_dynamics {
  background: #ffffff;
  margin-top: -10px;
  z-index: 2;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics {
  padding: 27px 31px 27px;
  display: flex;
  justify-content: space-between;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .tabs_wrap .buttons_dynamics {
  display: flex;
  justify-content: flex-start;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .tabs_wrap .buttons_dynamics .tab_clients,
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .tabs_wrap .buttons_dynamics .tab_revenue {
  padding: 8px 23px;
  border: 1px solid #00aa45;
  box-sizing: border-box;
  cursor: pointer;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .tabs_wrap .buttons_dynamics .tab_clients {
  border-radius: 4px 0px 0px 4px;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .tabs_wrap .buttons_dynamics .tab_revenue {
  margin-left: -1px;
  border-radius: 0px 4px 4px 0px;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .tabs_wrap .buttons_dynamics .active {
  background: #00aa45;
  color: #ffffff;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .info_message {
  display: flex;
  align-items: center;
  width: 490px;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .info_message .wrap_icon_wrong {
  margin-right: 7px;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .info_message .wrap_text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  color: #a99f9f;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .info_message .wrap_text .help {
  display: inline-block;
  position: relative;
  margin-left: 7px;
  height: 14px;
  width: 14px;
  bottom: -2px;
  box-sizing: border-box;
  cursor: pointer;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .info_message .wrap_text .help .icon {
  display: flex;
  align-items: center;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .info_message .wrap_text .help .information_block {
  cursor: default;
  position: absolute;
  visibility: hidden;
  top: 20px;
  right: 0px;
  background: #ffffff;
  line-height: 18px;
  font-size: 13px;
  color: #4d4d4d;
  width: 391px;
  padding: 5px 15px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transition: opacity 500ms linear, top 100ms linear,
                bottom 100ms linear, visibility 100ms linear;
  z-index: 4;
  transition-delay: 0.2s;
}
.wrap_main_content_dynamics .main_content_dynamics .tabs_dynamics .info_message .wrap_text .help:hover .information_block {
  visibility: visible;
  opacity: 1;
  transition-delay: 0.2s;
  z-index: 4;
}
