.tab_royalty {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  width: 442px;
  height: 130px;
  background: #e6e6e6;
  border-radius: 20px 20px 0px 0px;
}
.tab_royalty.active {
  background: #ffffff;
}
.tab_royalty .column_content_left,
.tab_royalty .column_content_right {
  padding-left: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 190px;
}
.tab_royalty .column_content_left .wrapper,
.tab_royalty .column_content_right .wrapper {
  margin-top: -3px;
  height: 130px;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}
.tab_royalty .column_content_left .revenue,
.tab_royalty .column_content_right .revenue,
.tab_royalty .column_content_left .royalty,
.tab_royalty .column_content_right .royalty {
  font-size: 10px;
  text-transform: uppercase;
  color: #7f7f7f;
}
.tab_royalty .column_content_left .revenue .revenue_text,
.tab_royalty .column_content_right .revenue .revenue_text,
.tab_royalty .column_content_left .royalty .revenue_text,
.tab_royalty .column_content_right .royalty .revenue_text {
  letter-spacing: 0.08em;
}
.tab_royalty .column_content_left .recalculation,
.tab_royalty .column_content_right .recalculation {
  margin-top: 3px;
  font-size: 10px;
  text-transform: uppercase;
  color: #7f7f7f;
}
.tab_royalty .column_content_left .recalculation .recalculation_text,
.tab_royalty .column_content_right .recalculation .recalculation_text {
  letter-spacing: 0.08em;
}
.tab_royalty .column_content_left .revenue_count,
.tab_royalty .column_content_right .revenue_count,
.tab_royalty .column_content_left .royalty_count,
.tab_royalty .column_content_right .royalty_count,
.tab_royalty .column_content_left .rub,
.tab_royalty .column_content_right .rub {
  margin-top: 9px;
  line-height: normal;
  font-size: 23px;
  color: #000000;
  text-transform: none;
}
.tab_royalty .column_content_left .recalculation_count,
.tab_royalty .column_content_right .recalculation_count {
  margin-top: 5px;
  line-height: 19px;
  font-size: 14px;
  text-transform: none;
}
.tab_royalty .column_content_left .recalculation_count.green,
.tab_royalty .column_content_right .recalculation_count.green {
  color: #00aa45;
}
.tab_royalty .column_content_left .recalculation_count.red,
.tab_royalty .column_content_right .recalculation_count.red {
  color: #ef5350;
}
.tab_royalty .wrap_sum {
  display: flex;
  align-items: baseline;
}
.tab_royalty .wrap_sum .rub {
  margin-left: 8px;
}
.tab_royalty .column_content_right {
  border-left: 1px dashed #bababa;
}
@media screen and (min-width: 1500px) {
  .tab_royalty {
    width: 442px;
  }
  .tab_royalty .column_content_left .revenue,
  .tab_royalty .column_content_right .revenue,
  .tab_royalty .column_content_left .royalty,
  .tab_royalty .column_content_right .royalty {
    font-size: 11px;
  }
  .tab_royalty .column_content_left .recalculation,
  .tab_royalty .column_content_right .recalculation {
    font-size: 11px;
  }
  .tab_royalty .column_content_left .recalculation_count,
  .tab_royalty .column_content_right .recalculation_count {
    font-size: 15px;
  }
}
