.tab {
  transition: all 0.3s;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.05);
  border-radius: 20px 20px 0px 0px;
  background: #e6e6e6;
  width: 300px;
  height: 130px;
}
.tab.active {
  background: #ffffff;
}
.tab .content_tab {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: -12px;
  height: 80px;
}
.tab .content_tab .clients {
  font-size: 23px;
  color: #3a3a3a;
  margin-bottom: 19px;
}
.tab .content_tab .bottom_block {
  margin-top: -7px;
}
.tab .content_tab .bottom_block .clients_bo,
.tab .content_tab .bottom_block .clients__no_bo {
  margin: 7px 4px;
  font-size: 10px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #7f7f7f;
}
.tab .content_tab .bottom_block .clients_bo .count_clients_bo,
.tab .content_tab .bottom_block .clients__no_bo .count_clients_bo,
.tab .content_tab .bottom_block .clients_bo .count_clients_no_bo,
.tab .content_tab .bottom_block .clients__no_bo .count_clients_no_bo {
  margin-right: 5px;
  color: #4d4d4d;
}
@media screen and (min-width: 1500px) {
  .tab {
    transition: all 0.3s;
    width: 343px;
  }
  .tab .content_tab .bottom_block .clients_bo,
  .tab .content_tab .bottom_block .clients__no_bo {
    font-size: 11px;
  }
}
