body {
  min-width: 1200px;
  font-family: Arial, Helvetica, sans-serif;
  background: #f2f2f2;
  margin: 8px 20px 40px;
  position: relative;
}
body .bg_img {
  position: absolute;
  top: 15px;
  right: 70px;
  background: url('./assets/images/image.svg') no-repeat;
  width: 329px;
  height: 385px;
  z-index: -1;
}
.scrollbar {
  position: absolute;
  right: 5px;
  top: 0;
  height: 100%;
  width: 9px;
  display: block;
}
.scrollbar .scroll {
  height: 100px;
  width: 9px;
  background: #a99f9f;
  border-radius: 50px;
  position: relative;
  right: 0;
  cursor: pointer;
}
.scrollbar .scroll.small {
  transition: all 0.3s;
}
.scrollbar .scroll.small:hover {
  transition: duration 0.3s;
  transform: scale(2);
}
h3 {
  font-weight: 300;
}
h2 {
  margin: 0;
}
a {
  text-decoration: none;
}
ul,
menu,
dir {
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
}
.wrap {
  position: relative;
}
.wrap .tabs {
  transition: all 0.3s;
  display: flex;
  justify-content: space-between;
  width: 1030px;
}
.wrap .gritter {
  position: fixed;
  bottom: 21px;
  right: 21px;
  width: 202px;
  background: #ffdc44;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 25px;
  z-index: 10;
}
.wrap .gritter .cross {
  cursor: pointer;
  position: absolute;
  top: 11px;
  right: 11px;
  padding: 5px;
}
.wrap .gritter .play_icon {
  margin-right: 17px;
}
.wrap .gritter .headline {
  display: flex;
  justify-content: flex-start;
  color: #3a3a3a;
  font-weight: bold;
  font-size: 14px;
}
.wrap .gritter .main_info {
  line-height: 16px;
  font-size: 12px;
  color: #3a3a3a;
  margin: 12px 0;
}
.wrap .gritter .watch {
  font-weight: 600;
  line-height: normal;
  font-size: 12px;
  text-decoration-line: underline;
  color: #3a3a3a;
}
@media screen and (min-width: 1500px) {
  .wrap .tabs {
    transition: all 0.3s;
    width: 1075px;
  }
}
