.wrap_service {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 0 107px;
}
.wrap_service .headline_block {
  display: flex;
  align-items: center;
}
.wrap_service .headline_block .color_indicator {
  padding: 5px;
  border-radius: 2px;
  margin-right: 7px;
}
.wrap_service .headline_block .color_indicator._0 {
  background: #c5c5c5;
}
.wrap_service .headline_block .color_indicator._1 {
  background: #ef5f50;
}
.wrap_service .headline_block .color_indicator._2 {
  background: #ffc700;
}
.wrap_service .headline_block .color_indicator._3 {
  background: #ffa726;
}
.wrap_service .headline_block .color_indicator._4 {
  background: #9acc00;
}
.wrap_service .headline_block .color_indicator._5 {
  background: #25b8b6;
}
.wrap_service .headline_block .color_indicator._6 {
  background: #4483cc;
}
.wrap_service .headline_block .color_indicator._7 {
  background: #9173d0;
}
.wrap_service .headline_block .color_indicator._8 {
  background: #ed89b7;
}
.wrap_service .headline_block .group_name {
  font-weight: bold;
  font-size: 14px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #808080;
}
.wrap_service .total_clients {
  font-weight: bold;
  font-size: 36px;
  line-height: normal;
  color: #5c5c5c;
  display: flex;
  margin: 30px 0;
}
.wrap_service .total_clients .count_clients {
  margin-right: 7px;
}
.wrap_service .end_last_month {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.wrap_service .end_last_month .label {
  font-size: 14px;
  line-height: normal;
  color: #8d8787;
}
.wrap_service .end_last_month .count {
  font-size: 20px;
  line-height: normal;
  color: #3a3a3a;
  margin-top: 12px;
}
.wrap_service .field {
  display: flex;
  justify-content: space-between;
  margin: 25px 0;
}
.wrap_service .field .line {
  flex: 1;
  height: 1px;
  background: #f4f4f4;
}
.wrap_service .field .name {
  font-size: 14px;
  line-height: 21px;
  color: #8d8787;
}
.wrap_service .field .right_block {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.wrap_service .field .right_block .color_count {
  display: flex;
  justify-content: flex-end;
  margin-right: 7px;
  font-size: 20px;
}
.wrap_service .field .right_block .color_count.neutral {
  color: #8d8787;
}
.wrap_service .field .right_block .color_count.plus {
  color: #00aa45;
}
.wrap_service .field .right_block .color_count.minus {
  color: #ef5350;
}
.wrap_service .field .right_block .help {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.wrap_service .field .right_block .help:hover .information_block {
  visibility: visible;
  opacity: 1;
  transition-delay: 0.2s;
  z-index: 4;
}
.wrap_service .field .right_block .help .information_block {
  cursor: default;
  position: absolute;
  visibility: hidden;
  top: 40px;
  right: 0px;
  background: #ffffff;
  line-height: 18px;
  font-size: 13px;
  color: #4d4d4d;
  width: 252px;
  padding: 5px 15px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transition: opacity 500ms linear, top 100ms linear,
            bottom 100ms linear, visibility 100ms linear;
  z-index: 4;
  transition-delay: 0.2s;
}
.wrap_service .field .right_block .help .information_block a {
  color: #4187a8;
}
