.wrap_transfer_on_technology {
  position: relative;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 35px;
}
.wrap_transfer_on_technology .inscription {
  position: absolute;
  top: 0;
  left: 40px;
}
.wrap_transfer_on_technology .inscription .headline_transfer {
  font-size: 22px;
  line-height: 30px;
  color: #3a3a3a;
  margin: 0;
  padding: 10px 0;
  font-style: normal;
  font-weight: normal;
}
