.wrap_window {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 99999;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
}
.wrap_window .window {
  position: relative;
  width: 538px;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 40px 50px 28px 50px;
}
.wrap_window .window .cross {
  cursor: pointer;
  position: absolute;
  top: 19px;
  right: 19px;
  padding: 7px;
}
.wrap_window .window .hello_text {
  font-weight: bold;
  line-height: 24px;
  font-size: 18px;
  text-align: center;
  color: #3a3a3a;
  margin: 0;
}
.wrap_window .window .wrap_label {
  line-height: 16px;
  font-size: 13px;
  text-align: center;
  color: #959595;
  margin: 20px 0;
}
.wrap_window .window .youtube_frame {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 241px;
  margin-bottom: 15px;
}
.wrap_window .window .youtube_frame iframe {
  width: 100%;
  height: 241px;
}
.wrap_window .window .btn_group {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: flex-end;
}
.wrap_window .window .btn_group .skip {
  background: #fedd46;
  border-radius: 5px;
  font-weight: bold;
  line-height: normal;
  font-size: 10px;
  letter-spacing: 0.05em;
  color: #3a3a3a;
  padding: 9px 13px;
  text-transform: uppercase;
}
.wrap_window .window .btn_group .clear {
  margin-left: 15px;
  font-weight: bold;
  line-height: normal;
  font-size: 10px;
  letter-spacing: 0.05em;
  color: #959595;
  text-transform: uppercase;
}
