.tab_dynamics {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 269px;
  height: 130px;
  font-size: 20px;
  color: #7f7f7f;
  background: #e6e6e6;
  border-radius: 20px 20px 0px 0px;
}
.tab_dynamics.active {
  background: #ffffff;
}
.tab_dynamics .dynamics_text {
  width: 140px;
  margin-left: 20px;
}
