.wrap_revenue {
  background: #ffffff;
  border-bottom: 1px solid #e5e5e5;
}
.wrap_revenue .revenue {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 23px 35px 23px 25px;
}
.wrap_revenue .revenue .service,
.wrap_revenue .revenue .one_time,
.wrap_revenue .revenue .on_technology,
.wrap_revenue .revenue .recovery {
  display: flex;
  justify-content: space-between;
  align-items: center;
  white-space: nowrap;
}
.wrap_revenue .revenue .service .count,
.wrap_revenue .revenue .one_time .count,
.wrap_revenue .revenue .on_technology .count,
.wrap_revenue .revenue .recovery .count {
  margin-right: 8px;
}
.wrap_revenue .revenue .service .text,
.wrap_revenue .revenue .one_time .text,
.wrap_revenue .revenue .on_technology .text,
.wrap_revenue .revenue .recovery .text {
  font-size: 15px;
  color: #00963d;
}
.wrap_revenue .revenue .green_block {
  display: flex;
  justify-content: center;
  margin: 10px;
  min-width: 135px;
  padding: 10px 25px;
  background: rgba(0, 170, 69, 0.1);
  border-radius: 5px;
  font-weight: bold;
  font-size: 21px;
  text-align: center;
  color: #00963d;
}
