.revenue_charts {
  padding: 0px 19px;
}
.revenue_charts .headline_clients {
  margin: 0;
  font-weight: normal;
  line-height: 28px;
  font-size: 20px;
  color: #4d4d4d;
}
.revenue_charts .tabs_revenue {
  display: flex;
  margin: 14px 0;
  cursor: pointer;
}
.revenue_charts .tabs_revenue .total_revenues,
.revenue_charts .tabs_revenue .on_technology,
.revenue_charts .tabs_revenue .no_technology {
  color: #979797;
  background: #ffffff;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  padding: 8px 12px;
}
.revenue_charts .tabs_revenue .total_revenues.active_parent,
.revenue_charts .tabs_revenue .on_technology.active_parent,
.revenue_charts .tabs_revenue .no_technology.active_parent {
  color: #00aa45;
}
.revenue_charts .tabs_revenue .total_revenues {
  border-radius: 4px 0px 0px 4px;
}
.revenue_charts .tabs_revenue .on_technology {
  position: relative;
  margin-left: -1px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.revenue_charts .tabs_revenue .on_technology .menu_technology {
  position: absolute;
  top: 34px;
  left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: #ffffff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.12);
  border-radius: 5px;
  z-index: 10;
}
.revenue_charts .tabs_revenue .on_technology .menu_technology .field_menu {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  white-space: nowrap;
  cursor: pointer;
  text-decoration: none;
  color: #979797;
}
.revenue_charts .tabs_revenue .on_technology .menu_technology .field_menu:first-child {
  padding-top: 15px;
  border-radius: 5px 5px 0 0;
}
.revenue_charts .tabs_revenue .on_technology .menu_technology .field_menu:last-child {
  padding-bottom: 15px;
  border-radius: 0 0 5px 5px;
}
.revenue_charts .tabs_revenue .on_technology .menu_technology .field_menu:hover {
  background: #f7f7f7;
}
.revenue_charts .tabs_revenue .on_technology .menu_technology .field_menu.active {
  color: #00aa45;
}
.revenue_charts .tabs_revenue .on_technology svg {
  margin-left: 5px;
}
.revenue_charts .tabs_revenue .no_technology {
  border-radius: 0px 4px 4px 0px;
  margin-left: -1px;
}
