}
.wrap_main_content_royalty .main_content_royalty {
  margin-top: -10px;
  z-index: 2;
}
.wrap_main_content_royalty .revenue_bo {
  padding: 35px 36px 0;
  display: flex;
  justify-content: space-between;
}
.wrap_main_content_royalty .revenue_bo .left_block {
