.wrap_clients_charts .clients_charts {
  padding: 0px 19px;
}
.wrap_clients_charts .clients_charts .headline_clients {
  margin: 0;
  font-weight: normal;
  line-height: 28px;
  font-size: 20px;
  color: #979797;
}
.wrap_clients_charts .clients_charts .tabs_clients {
  display: flex;
  margin: 14px 0;
  cursor: pointer;
  color: #979797;
}
.wrap_clients_charts .clients_charts .tabs_clients .clients,
.wrap_clients_charts .clients_charts .tabs_clients .on_technology,
.wrap_clients_charts .clients_charts .tabs_clients .no_technology {
  background: #ffffff;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  padding: 8px 12px;
}
.wrap_clients_charts .clients_charts .tabs_clients .clients {
  border-radius: 4px 0px 0px 4px;
}
.wrap_clients_charts .clients_charts .tabs_clients .on_technology {
  border-radius: 0px 4px 4px 0px;
  margin-left: -1px;
}
.wrap_clients_charts .clients_charts .tabs_clients .active {
  color: #00aa45;
}
.wrap_clients_charts .clients_charts .wrap_chart_down {
  padding: 0 34px;
}
.wrap_clients_charts .bottom_graph {
  margin-top: 50px;
  border-top: 1px solid #b8b7b7;
}
.wrap_clients_charts .bottom_graph .headline_tariffs {
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 30px;
  color: #3a3a3a;
  padding: 35px 22px;
}
