.wrap_main_content_clients {
  position: relative;
}
.wrap_main_content_clients .main_content_clients {
  background: #ffffff;
  margin-top: -10px;
  z-index: 2;
}
.wrap_main_content_clients .main_content_clients .green_numbers {
  padding: 25px 9px;
  border-bottom: 1px solid #e5e5e5;
  display: flex;
  justify-content: space-between;
}
.wrap_main_content_clients .main_content_clients .green_numbers .label_rating {
  transition: all 0.3s;
  color: #a99f9f;
  width: 444px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  flex: 1;
}
.wrap_main_content_clients .main_content_clients .green_numbers .label_rating__wrapper_text {
  flex: 1;
}
.wrap_main_content_clients .main_content_clients .green_numbers .label_rating__wrapper_text .text {
  font-size: 14px;
  line-height: 23px;
  padding-left: 101px;
}
.wrap_main_content_clients .complex_sevice {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e5e5e5;
  padding: 25px 0;
}
.wrap_main_content_clients .complex_sevice .left_block {
  padding: 0 30px;
  flex: 1;
}
.wrap_main_content_clients .complex_sevice .field_for_chart {
  flex: 1;
  display: flex;
  justify-content: center;
  padding-top: 8px;
}
.wrap_main_content_clients .clients_no_technology {
  height: 150px;
  border-bottom: 1px solid #e5e5e5;
}
.wrap_main_content_clients .transfer_on_technology_chart {
  display: flex;
  justify-content: flex-start;
  border-bottom: 1px dashed #e5e5e5;
  padding-top: 58px;
}
@media screen and (min-width: 1500px) {
  .wrap_main_content_clients .main_content_clients .green_numbers .label_rating {
    transition: all 0.3s;
    width: 590px;
  }
  .wrap_main_content_clients .main_content_clients .green_numbers .label_rating__wrapper_text .text {
    font-size: 16px;
    line-height: 23px;
    padding-left: 106px;
  }
}
