.green_numbers__items {
  display: flex;
  justify-content: flex-start;
  width: 800px;
  padding: 0px 39px 0 18px;
  flex: 1;
}
.green_numbers__items .green_item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  border-radius: 5px;
  flex-shrink: 0;
  background: #e5f6ec;
}
.green_numbers__items .green_item__count_in {
  font-weight: bold;
  font-size: 24px;
  text-align: center;
  color: #00963d;
}
.green_numbers__items .green_item__text {
  display: inline-block;
  margin-left: 15px;
  display: block;
  white-space: pre-line;
  font-size: 16px;
  color: #00963d;
}
.green_numbers__items .green_item.first {
  margin-right: 20px;
}
