.wrap_remain_transfer {
  width: 588px;
  flex: 1;
}
.wrap_remain_transfer ::-webkit-scrollbar {
  display: none;
}
.wrap_remain_transfer .headline {
  display: flex;
  justify-content: space-between;
  padding: 0 34px;
}
.wrap_remain_transfer .headline .cap {
  font-weight: bold;
  font-size: 14px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #808080;
}
.wrap_remain_transfer .headline .start_franchise {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: normal;
  color: #878787;
}
.wrap_remain_transfer .headline .start_franchise svg {
  margin-right: 8.5px;
}
.wrap_remain_transfer .label_wrong,
.wrap_remain_transfer .label_time_left {
  padding: 0px 12px;
  margin-left: 23px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.wrap_remain_transfer .label_wrong .wrap_icon_calendar,
.wrap_remain_transfer .label_time_left .wrap_icon_calendar {
  margin-right: 12px;
}
.wrap_remain_transfer .label_wrong .wrap_text,
.wrap_remain_transfer .label_time_left .wrap_text {
  line-height: 18px;
  font-size: 12px;
  color: #3a3a3a;
  display: inline-block;
  position: relative;
}
.wrap_remain_transfer .label_wrong .wrap_text .help,
.wrap_remain_transfer .label_time_left .wrap_text .help {
  margin-left: 7px;
  height: 14px;
  width: 14px;
  bottom: -2px;
  box-sizing: border-box;
}
.wrap_remain_transfer .label_wrong .help,
.wrap_remain_transfer .label_time_left .help {
  display: inline-block;
  position: relative;
  cursor: pointer;
}
.wrap_remain_transfer .label_wrong .help .icon,
.wrap_remain_transfer .label_time_left .help .icon {
  display: flex;
  align-items: center;
}
.wrap_remain_transfer .label_wrong .help .information_block,
.wrap_remain_transfer .label_time_left .help .information_block {
  cursor: default;
  position: absolute;
  visibility: hidden;
  top: 20px;
  right: 0px;
  background: #ffffff;
  line-height: 18px;
  font-size: 13px;
  color: #4d4d4d;
  width: 252px;
  padding: 5px 15px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transition: opacity 500ms linear, top 100ms linear, bottom 100ms linear,
          visibility 100ms linear;
  z-index: 4;
  transition-delay: 0.2s;
}
.wrap_remain_transfer .label_wrong .help:hover .information_block,
.wrap_remain_transfer .label_time_left .help:hover .information_block {
  visibility: visible;
  opacity: 1;
  transition-delay: 0.2s;
  z-index: 4;
}
.wrap_remain_transfer .container {
  height: 370px;
  position: relative;
  margin-top: 25px;
}
.wrap_remain_transfer .container .clients_list {
  height: 370px;
  width: 100%;
  padding-inline-start: 0;
  overflow: auto;
  -ms-overflow-style: none;
}
.wrap_remain_transfer .container .clients_list .item_client {
  padding: 0px 25px 0px 28px;
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 13px;
  color: #4d4d4d;
  border-top: 1px solid rgba(255, 255, 255, 0);
  border-bottom: 1px solid rgba(255, 255, 255, 0);
}
.wrap_remain_transfer .container .clients_list .item_client .client_name {
  padding: 8px;
  width: 200px;
}
.wrap_remain_transfer .container .clients_list .item_client .summ_on_period {
  display: flex;
  color: #a99f9f;
}
.wrap_remain_transfer .container .clients_list .item_client .summ_on_period .count_money {
  padding: 8px 5px;
}
.wrap_remain_transfer .container .clients_list .item_client .summ_on_period .period {
  padding: 8px 5px;
}
.wrap_remain_transfer .container .clients_list .item_client .wrap_links {
  display: none;
}
.wrap_remain_transfer .container .clients_list .item_client.left:hover {
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
}
.wrap_remain_transfer .container .clients_list .item_client.left:hover .summ_on_period .count_money {
  display: none;
}
.wrap_remain_transfer .container .clients_list .item_client.left:hover .summ_on_period .period {
  display: none;
}
.wrap_remain_transfer .container .clients_list .item_client.left:hover .summ_on_period .wrap_links {
  display: flex;
  align-items: center;
}
.wrap_remain_transfer .container .clients_list .item_client.left:hover .summ_on_period .wrap_links .btn {
  margin: 0 7px;
}
.wrap_remain_transfer .container .clients_list .item_client.left:hover .summ_on_period .wrap_links .link_on_technology {
  white-space: nowrap;
  padding: 8px 13px;
  background: #ffdc44;
  border-radius: 4px;
  font-weight: bold;
  line-height: normal;
  font-size: 10px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #e23835;
  text-decoration: none;
}
.wrap_remain_transfer .container .clients_list .item_client.left:hover .summ_on_period .wrap_links .link_on_technology svg {
  margin-left: 7px;
}
.wrap_remain_transfer .container .clients_list .item_client.left:hover .summ_on_period .wrap_links .edit {
  display: flex;
  align-items: center;
  font-weight: bold;
  line-height: normal;
  font-size: 10px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #a99f9f;
  text-decoration: none;
}
.wrap_remain_transfer .container .clients_list .item_client.left:hover .summ_on_period .wrap_links .dump {
  display: flex;
  align-items: center;
  font-weight: bold;
  line-height: normal;
  font-size: 10px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ef5350;
  text-decoration: none;
}
