.iframe {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10000;
}
.iframe iframe {
  width: 100%;
  height: 100vh;
  border: 0px;
}
.iframe .btn_back {
  padding: 15px 0;
  transition: all 0.3s;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f2f2f2;
  font-weight: bold;
  font-size: 16px;
  line-height: normal;
  color: #a2a1a1;
}
.iframe .btn_back:hover {
  transition: all 0.3s;
  color: #1f8acb;
  background: #f0f0f0;
}
.iframe .btn_back:hover svg path {
  fill: #1f8acb;
}
.iframe .btn_back .icon {
  margin-top: 3px;
  margin-right: 7px;
}
