.wrap_recalculation .recalculation .headline_table {
  font-weight: normal;
  line-height: 28px;
  font-size: 20px;
  color: #4d4d4d;
  padding: 19px 30px;
  display: flex;
  align-items: center;
}
.wrap_recalculation .recalculation .headline_table .help {
  display: inline-block;
  position: relative;
  margin-left: 7px;
  cursor: pointer;
}
.wrap_recalculation .recalculation .headline_table .help .icon {
  display: flex;
  align-items: center;
}
.wrap_recalculation .recalculation .headline_table .help .information_block {
  cursor: default;
  position: absolute;
  visibility: hidden;
  top: -80px;
  left: 0px;
  background: #ffffff;
  line-height: 18px;
  font-size: 13px;
  color: #4d4d4d;
  width: 272px;
  padding: 5px 15px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transition: opacity 500ms linear, top 100ms linear,
            bottom 100ms linear, visibility 100ms linear;
  z-index: 4;
  transition-delay: 0.2s;
}
.wrap_recalculation .recalculation .headline_table .help:hover .information_block {
  visibility: visible;
  opacity: 1;
  transition-delay: 0.2s;
  z-index: 4;
}
.wrap_recalculation .recalculation .container {
  position: relative;
}
.wrap_recalculation .recalculation .container .scroll_body {
  width: 100%;
  max-height: 154px;
  overflow-y: auto;
  -ms-overflow-style: none;
}
.wrap_recalculation .recalculation ::-webkit-scrollbar {
  display: none;
}
.wrap_recalculation .recalculation .table_recalculation,
.wrap_recalculation .recalculation .scroll_body > .table_body {
  width: 100%;
  font-size: 14px;
  border-collapse: collapse;
  z-index: 66;
}
.wrap_recalculation .recalculation .table_recalculation .wrap_headline,
.wrap_recalculation .recalculation .scroll_body > .table_body .wrap_headline {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.wrap_recalculation .recalculation .table_recalculation .wrap_headline .element_right,
.wrap_recalculation .recalculation .scroll_body > .table_body .wrap_headline .element_right,
.wrap_recalculation .recalculation .table_recalculation .wrap_headline .element_left,
.wrap_recalculation .recalculation .scroll_body > .table_body .wrap_headline .element_left {
  font-size: 10px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #7f7f7f;
  font-weight: normal;
  border-bottom: 1px solid #e5e5e5;
  border-top: 1px solid #e5e5e5;
}
.wrap_recalculation .recalculation .table_recalculation .wrap_headline .element_right,
.wrap_recalculation .recalculation .scroll_body > .table_body .wrap_headline .element_right {
  padding: 10px 30px 10px 0px;
  text-align: right;
}
.wrap_recalculation .recalculation .table_recalculation .wrap_headline .element_left,
.wrap_recalculation .recalculation .scroll_body > .table_body .wrap_headline .element_left {
  padding: 10px 0px 10px 30px;
  text-align: left;
}
.wrap_recalculation .recalculation .table_recalculation .field,
.wrap_recalculation .recalculation .scroll_body > .table_body .field {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.wrap_recalculation .recalculation .table_recalculation .field .element_right,
.wrap_recalculation .recalculation .scroll_body > .table_body .field .element_right,
.wrap_recalculation .recalculation .table_recalculation .field .element_left,
.wrap_recalculation .recalculation .scroll_body > .table_body .field .element_left {
  font-size: 14px;
  color: #4d4d4d;
}
.wrap_recalculation .recalculation .table_recalculation .field:hover,
.wrap_recalculation .recalculation .scroll_body > .table_body .field:hover {
  background: #e5e5e5;
  transition: all 0.3s;
}
.wrap_recalculation .recalculation .table_recalculation .field .element_right,
.wrap_recalculation .recalculation .scroll_body > .table_body .field .element_right {
  padding: 10px 30px 10px 0px;
  text-align: right;
}
.wrap_recalculation .recalculation .table_recalculation .field .element_left,
.wrap_recalculation .recalculation .scroll_body > .table_body .field .element_left {
  padding: 10px 0px 10px 30px;
  text-align: left;
}
.wrap_recalculation .recalculation .table_recalculation .field .revenue.green,
.wrap_recalculation .recalculation .scroll_body > .table_body .field .revenue.green,
.wrap_recalculation .recalculation .table_recalculation .field .royalty.green,
.wrap_recalculation .recalculation .scroll_body > .table_body .field .royalty.green {
  color: #00AA45;
}
.wrap_recalculation .recalculation .table_recalculation .field .revenue.red,
.wrap_recalculation .recalculation .scroll_body > .table_body .field .revenue.red,
.wrap_recalculation .recalculation .table_recalculation .field .royalty.red,
.wrap_recalculation .recalculation .scroll_body > .table_body .field .royalty.red {
  color: #ef5350;
}
.wrap_recalculation .recalculation .table_recalculation .element_left,
.wrap_recalculation .recalculation .scroll_body > .table_body .element_left {
  width: calc(70% / 3);
}
.wrap_recalculation .recalculation .table_recalculation .element_right,
.wrap_recalculation .recalculation .scroll_body > .table_body .element_right {
  width: calc(30% / 2);
}
.wrap_recalculation .recalculation .table_recalculation .dotted_border,
.wrap_recalculation .recalculation .scroll_body > .table_body .dotted_border {
  border-right: 1px dashed #e5e5e5;
}
