.wrap_regular_service {
  border-top: 1px solid #e5e5e5;
}
.wrap_regular_service .regular_service .top_block {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 161px;
}
.wrap_regular_service .regular_service .top_block .headline {
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 31px;
  margin: 0 30px;
  color: #3a3a3a;
}
.wrap_regular_service .regular_service .top_block .label_under_charts {
  line-height: 19px;
  font-size: 14px;
  color: #a99f9f;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body {
  width: 100%;
  font-size: 14px;
  border-collapse: collapse;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .wrap_headline,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .wrap_headline {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .wrap_headline .element_right,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .wrap_headline .element_right,
.wrap_regular_service .regular_service .footer_block .table_recalculation .wrap_headline .element_left,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .wrap_headline .element_left {
  font-size: 10px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #7f7f7f;
  font-weight: normal;
  border-bottom: 1px solid #e5e5e5;
  border-top: 1px solid #e5e5e5;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .wrap_headline .empty,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .wrap_headline .empty {
  user-select: none;
  color: #ffffff;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .wrap_headline .element_right,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .wrap_headline .element_right {
  padding: 10px 30px 10px 0px;
  text-align: right;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .wrap_headline .element_left,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .wrap_headline .element_left {
  padding: 10px 0px 10px 30px;
  text-align: left;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field .element_right,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field .element_right,
.wrap_regular_service .regular_service .footer_block .table_recalculation .field .element_left,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field .element_left {
  font-size: 14px;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field:hover,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field:hover {
  background: #e5e5e5;
  transition: all 0.3s;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field .element_right,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field .element_right {
  padding: 10px 30px 10px 0px;
  text-align: right;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field .element_left,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field .element_left {
  padding: 10px 0px 10px 30px;
  text-align: left;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field .status,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field .status {
  font-weight: bold;
  font-size: 9px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field .green,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field .green {
  color: #00963d;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field .blue,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field .blue {
  color: #0972ec;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field .gray,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field .gray {
  color: #a99f9f;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .field .red,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .field .red {
  color: #fb2626;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .element_left,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .element_left {
  color: #4d4d4d;
  width: calc(65% / 4);
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .element_right,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .element_right {
  color: #4d4d4d;
  width: calc(30% / 2);
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .empty,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .empty {
  width: 2%;
  color: #7f7f7f;
}
.wrap_regular_service .regular_service .footer_block .table_recalculation .dotted_border,
.wrap_regular_service .regular_service .footer_block .scroll_body > .table_body .dotted_border {
  border-right: 1px dashed #e5e5e5;
}
.wrap_regular_service .regular_service .footer_block .container {
  position: relative;
}
.wrap_regular_service .regular_service .footer_block .container .scroll_body {
  width: 100%;
  max-height: 260px;
  overflow-y: auto;
  -ms-overflow-style: none;
}
.wrap_regular_service .regular_service .footer_block .container .scroll_body .table_body tr:hover {
  background: #e5e5e5;
  opacity: 1;
  transition: all 0.3s;
}
.wrap_regular_service .regular_service .footer_block ::-webkit-scrollbar {
  display: none;
}
.wrap_regular_service .regular_service .top_block {
  position: relative;
}
.wrap_regular_service .regular_service .top_block .wrap_chart {
  position: absolute;
  top: 0;
  right: 0;
  left: 447px;
}
.wrap_regular_service .regular_service .top_block .wrap_chart label {
  position: absolute;
  top: 126px;
  left: 20px;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div {
  position: absolute;
  height: 400px !important;
  top: -110px;
  z-index: 4;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table {
  margin-top: 120px;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  padding: 10px;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table .tb_green {
  width: 311px;
  z-index: 43333;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table .tb_green .green_text {
  font-weight: bold;
  line-height: normal;
  font-size: 9px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #00963d;
  text-align: left;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table .tb_green .num {
  line-height: 21px;
  font-size: 13px;
  text-align: right;
  color: #4d4d4d;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table .tb_red {
  width: 311px;
  z-index: 43333;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table .tb_red .text {
  font-weight: bold;
  line-height: normal;
  font-size: 9px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  text-align: left;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table .tb_red .gray_text {
  color: #a99f9f;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table .tb_red .blue_text {
  color: #0972ec;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table .tb_red .red_text {
  color: #fb2626;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-chart-div .wrap_table .tb_red .num {
  line-height: 21px;
  font-size: 13px;
  text-align: right;
  color: #4d4d4d;
}
.wrap_regular_service .regular_service .top_block .wrap_chart .amcharts-legend-div {
  position: absolute;
  z-index: 1;
  font-weight: bold;
  line-height: normal;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #4e4867;
  white-space: nowrap;
}
