.wrap_charts_bo1c .headline {
  font-style: normal;
  font-weight: normal;
  line-height: 28px;
  font-size: 20px;
  color: #4d4d4d;
  margin: 34px;
}
.wrap_charts_bo1c .wrap_chart_down_regular_service {
  margin-top: 50px;
  border-top: 1px solid #b8b7b7;
}
.wrap_charts_bo1c .wrap_chart_down_regular_service .active {
  fill: #00aa45;
}
.wrap_charts_bo1c .title_legend {
  font-weight: normal;
  line-height: 28px;
  font-size: 20px;
  color: #4d4d4d;
  margin-left: 34px;
}
.wrap_charts_bo1c .legend_and_chart {
  display: flex;
  justify-content: space-between;
  margin-left: 34px;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend {
  position: relative;
  border-right: #8d8787;
  margin-top: 26px;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item {
  display: flex;
  flex-direction: column;
  margin: 8px 0;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .label {
  line-height: 18px;
  font-size: 13px;
  color: #8d8787;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money {
  display: flex;
  align-items: center;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .dot,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .dot {
  width: 15px;
  height: 15px;
  border-radius: 50px;
  margin-right: 5px;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .dot.green,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .dot.green {
  background: #00aa45;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .dot.light_green,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .dot.light_green {
  background: #85c3b8;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .money,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .money {
  line-height: 28px;
  font-size: 20px;
  color: #4d4d4d;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .red,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .red {
  color: #ef5350;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .red.triangle,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .red.triangle {
  background: #ef5350;
  border-radius: 10px;
  width: 20px;
  margin-right: 5px;
  position: relative;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .red.triangle:after,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .red.triangle:after {
  content: "";
  position: absolute;
  /* Абсолютное позиционирование */
  left: 0px;
  bottom: -12px;
  /* Положение треугольника */
  border: 7px solid transparent;
  border-top: 9px solid #ef5350;
  border-radius: 2px;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .green,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .green {
  color: #00aa45;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .green.triangle,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .green.triangle {
  background: #00aa45;
  border-radius: 10px;
  width: 20px;
  margin-right: 5px;
  position: relative;
}
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_dot_and_money .green.triangle:after,
.wrap_charts_bo1c .legend_and_chart .dynamic_legend .item .wrap_triangle_and_money .green.triangle:after {
  content: "";
  position: absolute;
  /* Абсолютное позиционирование */
  left: 0px;
  top: -12px;
  /* Положение треугольника */
  border: 7px solid transparent;
  border-bottom: 9px solid #00aa45;
  border-radius: 2px;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table {
  flex: 1;
  display: flex;
  justify-content: center;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table {
  min-width: 354px;
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 34px 107px 34px 0;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .field {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .field .line {
  flex: 1;
  height: 1px;
  background: #f4f4f4;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .field .name {
  font-size: 14px;
  line-height: 21px;
  color: #8d8787;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .field .right_block {
  display: flex;
  justify-content: flex-end;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .field .right_block .color_count {
  display: flex;
  justify-content: flex-end;
  white-space: nowrap;
  margin-right: 7px;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .field .right_block .color_count.neutral {
  color: #8d8787;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .field .right_block .color_count.plus {
  color: #00aa45;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .field .right_block .color_count.minus {
  color: #ef5350;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .field .right_block .rub {
  margin-left: 7px;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .help {
  position: relative;
  cursor: pointer;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .help:hover .information_block {
  visibility: visible;
  opacity: 1;
  transition-delay: 0.2s;
  z-index: 4;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .help .information_block {
  cursor: default;
  position: absolute;
  visibility: hidden;
  top: 40px;
  right: 0px;
  background: #ffffff;
  line-height: 18px;
  font-size: 13px;
  color: #4d4d4d;
  width: 282px;
  padding: 5px 15px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transition: opacity 500ms linear, top 100ms linear,
              bottom 100ms linear, visibility 100ms linear;
  z-index: 4;
  transition-delay: 0.2s;
}
.wrap_charts_bo1c .legend_and_chart .wrap_value_table .value_table .help .information_block a {
  color: #4187a8;
}
.wrap_charts_bo1c .legend_and_chart .right_block {
  flex: 1;
  display: flex;
}
.wrap_charts_bo1c .legend_and_chart .right_block table {
  min-width: 300px;
  font-size: 14px;
  color: #4d4d4d;
  padding: 18px 5px;
}
.wrap_charts_bo1c .legend_and_chart .right_block table td {
  padding: 12px 5px;
}
